import { Link } from "gatsby"
import React, { Component } from "react"
import header from "./header.module.scss"
import Headroom from "react-headroom"
import { connect } from "react-redux"
import { changeAuth } from "../actions/changeAuth"

import { FontAwesomeIcon } from "@fortawesome/react-fontawesome"
import { faBars } from "@fortawesome/free-solid-svg-icons"

class Header extends Component {
  state = {
    open: false,
  }

  signOut = async () => {
    await fetch(process.env.API + "signOut", {
      mode: "cors", // no-cors, cors, *include
      cache: "no-cache", // *default, no-cache, reload, force-cache, only-if-cached
      credentials: "include", // include, *include, omit
      method: "GET",
    })
    this.props.changeAuth(false)
  }

  openNav = () => {
    this.setState({ open: !this.state.open })
  }
  render() {
    const stateStyle = this.state.open ? "0px" : "-60px"
    return (
      <Headroom disableInlineStyles="true">
        <div className={header.topnav}>
          <div className={header.rows}>
            <Link className={header.brand} to="/">
              Wozz
            </Link>{" "}
            {this.props.isAuthed && (
              <>
                <Link to="/userAuth">
                  <button
                    onClick={() => {
                      this.signOut()
                    }}
                  >
                    Logout
                  </button>
                </Link>
              </>
            )}
            {!this.props.isAuthed && (
              <>
                {" "}
                <Link to="/signin">
                  <button>Sign In</button>
                </Link>
                <Link to="/signup">
                  <button>Sign Up</button>
                </Link>
              </>
            )}
          </div>
          <div className={header.rows}>
            <FontAwesomeIcon
              onClick={this.openNav}
              className={header.bars}
              icon={faBars}
            ></FontAwesomeIcon>
          </div>
        </div>

        <div className={header.bottomnav} style={{ marginTop: stateStyle }}>
          <Link
            onClick={this.openNav}
            className={header.bottomFirst}
            to="/settings"
          >
            My Settings
          </Link>
          <Link
            onClick={this.openNav}
            className={header.bottomFirst}
            to="/wrapup"
          >
            Weekly Report
          </Link>
        </div>
      </Headroom>
    )
  }
}

function mapStateToProps(state) {
  return { isAuthed: state.authStatus }
}

export default connect(
  mapStateToProps,
  { changeAuth }
)(Header)
