import {
  ADD_EVENT,
  RESET_EVENT,
  DELETE_EVENT,
  GET_EVENTS,
} from "../actions/types"

import { decode } from "../utilities/decode"

export default function handleEvents(state = [], action) {
  switch (action.type) {
    case ADD_EVENT:
      const newEvent = action.payload
      return [...state, newEvent]
    case RESET_EVENT:
      return action.payload

    case DELETE_EVENT:
      // const decodedDeleteEvents = action.payload.map(e => {
      //   return { ...e, title: decode(e.title) }
      // })
      return action.payload

    case GET_EVENTS:
      return action.payload

    default:
      return state
  }
}
