import React, { Component } from "react"
import { connect } from "react-redux"
import Header from "./header"
import Footer from "./footer"
import layout from "./layout.module.scss"
import { changeAuth } from "../actions/changeAuth"
import { addToken } from "../actions/addToken"
import { setCategories } from "../actions/setCategories"
import Spinner from "./spinner"
import { getEvents } from "../actions/getEvents"
import "../main.scss"

class Layout extends Component {
  state = {
    isLoading: true,
  }
  checkAuth = async () => {
    const response = await fetch(process.env.API + "checkAuth", {
      mode: "cors", // no-cors, cors, *include
      cache: "no-cache", // *default, no-cache, reload, force-cache, only-if-cached
      credentials: "include", // include, *include, omit
      headers: {
        "content-type": "application/json",
      },
      method: "GET",
    })
    response.json().then(data => {
      if (data.authStatus === true) {
        this.props.changeAuth(true)
        this.props.addToken(data.token)
        this.props.setCategories(data.user.categories)
        this.props.getEvents(data.user.events)
        this.setState({ isLoading: false })
      } else if (data.authStatus === false) {
        this.props.changeAuth(false)
        this.props.addToken(data.token)
        this.setState({ isLoading: false })
      }
    })
  }

  componentDidMount() {
    this.checkAuth()
  }

  render() {
    if (this.state.isLoading == false) {
      return (
        <div className={layout.container}>
          <Header />
          <div className={layout.content}>{this.props.children}</div>
          <Footer />
        </div>
      )
    }
    return <Spinner />
  }
}

function mapStateToProps(state) {
  return {
    auth: state.authStatus,
    category: state.activeCategory,
    events: state.events,
  }
}

export default connect(
  mapStateToProps,
  { changeAuth, setCategories, getEvents, addToken }
)(Layout)
