import { ADD_CATEGORY, DELETE_CATEGORY, SET_CATEGORY } from "../actions/types"

const initialState = []

export default function handleCategories(state = initialState, action) {
  switch (action.type) {
    case SET_CATEGORY:
      return action.payload
    case ADD_CATEGORY:
      return action.payload
    case DELETE_CATEGORY:
      return action.payload
    default:
      return state
  }
}
